import React, { Component } from 'react'
import style from "./Contact.module.css"
export default class Contact extends Component {
    render() {
        return (
            <div className="my-5 pt-5">
                <div className="text-center my-3">
                    <h2 className="pageHeader ko7lyColor">Contact</h2>

                    <span className=" starIcon bg-ko7ly translate-middle-y ">
                        <i class="fas fa-star ko7lyColor "></i>
                    </span>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-7 m-auto mb-3">
                            <div className="form-group mb-4">
                                 <label htmlFor="name" className="greenColor  ">Name</label>
                                <input type="text" id="name"placeholder="Name" className=" w-100  border-0 bg-transparent" />
                               
                            </div>
                            <div className={style.line}></div>
                        </div>
                        <div className="col-md-7 m-auto mb-3">
                            <div className="form-group mb-4">
                                 <label htmlFor="email" className="greenColor  ">Email</label>
                                <input type="email" id="email"placeholder="Email" className=" w-100  border-0 bg-transparent" />
                               
                            </div>
                            <div className={style.line}></div>
                        </div>
                        <div className="col-md-7 m-auto mb-3">
                            <div className="form-group mb-4">
                                 <label htmlFor="Phone Number" className="greenColor  ">Phone Number</label>
                                <input type="text" id="Phone Number"placeholder="Phone Number" className=" w-100  border-0 bg-transparent" />
                               
                            </div>
                            <div className={style.line}></div>
                        </div>
                        <div className="col-md-7 m-auto  mb-3">
                            <div className="form-group mb-4">
                                 <label htmlFor="message" className="greenColor  ">Message</label>
                              <textarea  name="message" placeholder="Message" id="message" className=" w-100 border border-1 border-success"  rows="5"></textarea>
                               
                            </div>
                             <button className={`${style.sendBtn} btn py-3 px-5 bg-green text-white`}>Send</button>
                        </div>
                   
                    </div>
                    
                </div>

            </div>
        )
    }
}
