    //     lightBox.style.display = "none";
    // }

    // showImage=()=>{
    //     this.LightBoxComponentRef.current.style.display="block";
    // }
    showImage = (imgId, imgTitle) => {
        this.LightBoxComponentRef.current.showImage();
        this.setState({
            imgId: imgId,
            imgTitle: imgTitle
        })
    }
