import React, { Component } from 'react'
import { Link } from 'react-router-dom'
// import style from 
export default class MyNavbar extends Component {
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark fixed-top background-ko7ly ">
  <div className="container px-5">
  <Link activeClassName="active" className="nav-link myLink   navbar-brand" aria-current="page" to="/home">Start React </Link>
     
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link activeClassName="active" className="nav-link myLink active" aria-current="page" to="/home">Home</Link>
          {/* ../HomeComponent/Home.jsx */}
        </li>
        <li className="nav-item">
        {/* ../AboutComponent/About.jsx */}
          <Link activeClassName="active" className="nav-link myLink" to="/about">About</Link>
        </li>
        <li className="nav-item">
          <Link activeClassName="active" className="nav-link myLink" to="/portofolia">Portofolia</Link>
        </li>
        <li className="nav-item">
          <Link activeClassName="active" className="nav-link myLink" to="/contact">Contact</Link>
        </li>
      
      </ul>
       
    </div>
  </div>
</nav>
            </div>
        )
    }
}
