import React, { Component } from 'react'
import style from './LightBox.module.css'
export default class LightBox extends Component {

    constructor(props) {
        super(props)
    
         
        this.itemRef=React.createRef(); 
    }
    
    closeLightBox=()=>{
        this.itemRef.current.style.display="none";
        document.body.style="overflow:auto";
    }
    showImage=()=>{
        this.itemRef.current.style.display="block";
        document.body.style="overflow:hidden";
    }
    render() {
        return (
            <div id={style.lightBox} ref={this.itemRef} class={ ` ${style.lightBox} position-fixed top-0 start-0 bottom-0 end-0 `}>
            <div className={`${style.container} position-relative bg-white text-center ko7lyColor p-4`}>
                <div className="row justify-content-center ">
                    <div className="col-md-8 ">
                        <h2 className={`${style.lightBoxHeader} text-uppercase fw-bolder   `}  >{this.props.title}</h2>
                        <div className=" stardiv justify-content-center align-items-center w-100 text-center">
                            <span className="starIcon bg-ko7ly"> <i className="ko7lyColor fas fa-star "></i></span>
                        </div>
                        <img src={this.props.imgId} className="w-100 rounded-3 my-5" alt="" />
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Mollitia neque assumenda ipsam nihil, molestias
                            magnam, recusandae quos quis inventore quisquam velit asperiores, vitae? Reprehenderit soluta, eos quod
                            consequuntur itaque. Nam. </p>

                        <button onClick={() => this.closeLightBox()} id={style.closeBtn} className={`btn bg-green px-3 py-2 text-white rounded-3 fw-bold border border-1 ${style.btn}`}> <span> <i
                            className="fas fa-times pe-2"></i> </span> Close Window</button>
                    </div>
                </div>
                <div id={style.closeIcon} onClick={() => this.closeLightBox()}  ><i className="fas fa-times greenColor"></i></div>
            </div>

        </div>
        )
    }
}
